'use client';
import React, { PropsWithChildren } from 'react';
import { cn } from '@/lib/cn';

export default function CardShell({
  children,
  className,
  style,
  bgImage,
}: PropsWithChildren<{ className?: string; style?: React.CSSProperties; bgImage?: string }>) {
  return (
    <div className="fixed inset-0 flex items-center justify-center bg-black/5">
      <div
        className={cn(
          'relative overflow-hidden rounded-sm shadow-xl',
          'portrait:w-[92vw] portrait:aspect-[1/1.618] portrait:max-h-[92vh]',
          'landscape:h-[92vh] landscape:aspect-[1.618/1] landscape:max-w-[92vw]',
          'bg-cover bg-center bg-no-repeat',
          className
        )}
        style={{
          // only set the image inline so the size/position can be overridden by classes
          backgroundImage: bgImage ? `url(${bgImage})` : undefined,
          ...style,
        }}
      >
        <div className="pointer-events-none absolute inset-0 bg-gradient-to-b from-black/30 via-black/20 to-black/40" />
        <div className="relative h-full w-full overflow-hidden text-shadow-2xs text-shadow-black">{children}</div>
      </div>
    </div>
  );
}
